<script>
  import bottomNav from '../components/bottomNav'
  import fold from '../components/fold'
  export default {
    data(){
      return{
        isShow:false,
        ishold:true,
        bottom_bg:require('../assets/img/bottom_bg.png'),
        banner_bg:require('../assets/img/banner_one_zh.jpg'),
        steps:0,
        automatic:true, //控制轮播 自动切换
/*        sliderImg:{
          sliderRu:[require('../assets/img/banner_one_ru.jpg'),require('../assets/img/banner_second_ru.jpg'),require('../assets/img/banner_three_ru.jpg'),require('../assets/img/banner_four_ru.jpg')],
          sliderZh:[require('../assets/img/banner_one_zh.jpg'),require('../assets/img/banner_second_zh.jpg'),require('../assets/img/banner_three_zh.jpg'),require('../assets/img/banner_four_zh.jpg')],
          sliderEn:[require('../assets/img/banner_one_en.jpg'),require('../assets/img/banner_second_en.jpg'),require('../assets/img/banner_three_en.jpg'),require('../assets/img/banner_four_en.jpg')]
        }*/
        sliderImg:[require('../assets/img/banner_one_ru.jpg'),require('../assets/img/banner_second_ru.jpg'),require('../assets/img/banner_three_ru.jpg'),require('../assets/img/banner_four_ru.jpg')]
        // SliderIndex:3
      }
    },
    mounted(){
      /*拿到路由传过来的参数*/
      var param = this.$route.params.anchor;
      if(param != undefined){
        this.pulley_roll(param);
      }


      this.$nextTick(function () {
        window.addEventListener('scroll', this.onScroll)
      })

    },
    methods:{
      nav_hover(){
        this.isShow = true;
      },
      nav_leave(){
        this.isShow = false;
      },
      /*控制 滚动条滚动的函数*/
      pulley_roll(val){
        var gac = $('#gac');
        var industrys = $('#industrys');
        var contact_we = $('#contact_we');
        gac = gac.offset().top;
        industrys = industrys.offset().top;
        contact_we = contact_we.offset().top;
        val == 1? $("html,body").animate({scrollTop:gac},1000):false;
        val == 2? $("html,body").animate({scrollTop:industrys},1000):false;
        val == 3? $("html,body").animate({scrollTop:contact_we},1000):false;
      },
      toScroll(val){
       this.pulley_roll(val);
      },
      open_faceplate(){
        // var h = $("#faceplate1").height();
        $("#faceplate1").animate({height:200});
      },
      goNews(){
        this.$router.push('/news');
      },
      onScroll () {
        let scrolled = document.documentElement.scrollTop || document.body.scrollTop;
        // console.log(scrolled);
        var gac = $('#gac').offset().top;
        var industrys = $('#industrys').offset().top;
        var news = $('#news').offset().top;
        var contact_we = $('#contact_we').offset().top;
        // console.log(gac,industrys,news,contact_we);
        scrolled += 200;
        if (scrolled >= contact_we) {
          this.steps = 4;
        } else if (scrolled < contact_we && scrolled >= news) {
          this.steps = 3;
        }else  if(scrolled < news && scrolled >= industrys){
          this.steps = 2;
        }else  if(scrolled < industrys && scrolled >= gac){
          this.steps = 1;
        }
        else {
          this.steps = 0;
        }
      },
      handleCommand(data){
        // this.sliderImg = [];



        // data == 1? this.$i18n.locale = 'zh':false;
        // data == 2? this.$i18n.locale = 'en':false;
        // data == 3? this.$i18n.locale = 'ru':false;

        if(data == 1){
          this.$i18n.locale = 'zh'
          this.sliderImg = [require('../assets/img/banner_one_zh.jpg'),require('../assets/img/banner_second_zh.jpg'),require('../assets/img/banner_three_zh.jpg'),require('../assets/img/banner_four_zh.jpg')];
        }
        if(data == 2){
          this.$i18n.locale = 'en'
          this.sliderImg = [require('../assets/img/banner_one_en.jpg'),require('../assets/img/banner_second_en.jpg'),require('../assets/img/banner_three_en.jpg'),require('../assets/img/banner_four_en.jpg')];
        }
        if(data == 3){
          this.$i18n.locale = 'ru';
          this.sliderImg = [require('../assets/img/banner_one_ru.jpg'),require('../assets/img/banner_second_ru.jpg'),require('../assets/img/banner_three_ru.jpg'),require('../assets/img/banner_four_ru.jpg')];
        }
      },
      go(val){
          this.$router.push(val);
      },
      NavHover(val){
        // console.log(val)
        // val == 0?this.SliderIndex = 0:console.log(this.SliderIndex);
        // val == 1?this.SliderIndex = 1:console.log(this.SliderIndex);
        // val == 2?this.SliderIndex = 2:console.log(this.SliderIndex);
        // val == 3?this.SliderIndex = 3:console.log(this.SliderIndex);
      },
      /*底部子组件传递过来的参数*/
      headCall(msg){
        var val = msg.params.anchor;
        this.pulley_roll(val);
      },
      /*右侧锚点*/
      anchors(val){
        var gac = $('#gac');
        var industrys = $('#industrys');
        var news = $('#news');
        var contact_we = $('#contact_we');
        gac = gac.offset().top;
        industrys = industrys.offset().top;
        news = news.offset().top;
        contact_we = contact_we.offset().top;
        val == 1? $("html,body").animate({scrollTop:0},1000):false;
        val == 2? $("html,body").animate({scrollTop:gac},1000):false;
        val == 3? $("html,body").animate({scrollTop:industrys},1000):false;
        val == 4? $("html,body").animate({scrollTop:news},1000):false;
        val == 5? $("html,body").animate({scrollTop:contact_we},1000):false;
      },
      setActiveItem(index) {
        this.automatic = false;
        this.$refs.carousel.setActiveItem(index);
      },
      leaves(){
        this.automatic = true;
      }

    },
    components:{
      bottomNav,
      fold
    }
  }
</script>

