          padding-left: 20%;
          padding-right: 20%;
          padding-top: 90px;
          box-sizing: border-box;
          text-align: center;
          color: #fff;
          h3{
            font-size: 25px;
            font-weight: normal;
            margin-bottom: 20px;
          }
        }
      }
    }
  }
  .contact_we{
    height: 998px;
    /*background: url('../assets/img/map.png') no-repeat;*/
    /*position: relative;*/
    .bg_img{
      .contact_us_con{
        position: absolute;
        right: 303px;
        top: 215px;
        width: 431px;
        height: 547px;
        background: rgba(255,255,255,0.8);
        padding: 110px 115px 0 115px;
        box-sizing: border-box;
        z-index: 999;
        h3{
          font-size: 30px;
          color: #000;
          text-align: center;
          font-weight: normal;
        }
        .img{
          text-align: center;
          margin-top: 30px;
          margin-bottom: 80px;
        }
        .details{
          p{
            font-family: FuturaLTBookOblique;
            line-height: 25px;
          }

        }
      }
      .flash{
        width: 60px;
        height: 60px;
        border-radius: 50%;
        background: rgba(0,0,0,0.2);
        position: absolute;
        top: 380px;
        left: 374px;
        animation: mymove .4s ease-in-out 0s infinite alternate;
        .flicker{
          position: absolute;
          width: 30px;
          height: 30px;
          top: 15px;
          left: 15px;
          border-radius: 50%;
          background: #b92020;
        }
        @keyframes mymove{
          from {opacity:1}
          to {opacity:0.1}
        }
      }
    }

  }
  .right_anchor{
    position: fixed;
    right: 30px;
    top: 30%;
    width: 20px;
    height: 100px;
    z-index: 999;
    ul{
      li{
        width: 15px;
        height: 15px;
        border-radius: 100%;
        margin: 0 auto;
        background: #fff;
      }
      .dot{
        cursor: pointer;
      }
      .line{
        width: 2px;
        height: 40px;
        background: none;
        text-align: center;
        border-radius: 0;
      }
    }
  }
</style>



