// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import './assets/base/base.css'
import './assets/jq/jquery-1.9.1.min'
import { Collapse,Dropdown,DropdownMenu,DropdownItem,Row,Col,CollapseItem,Pagination,Carousel,CarouselItem } from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';




Vue.use(Collapse);
Vue.use(Dropdown);
Vue.use(DropdownMenu);
Vue.use(DropdownItem);
Vue.use(Row);
Vue.use(Col);
Vue.use(CollapseItem);
Vue.use(Pagination);
Vue.use(Carousel);
Vue.use(CarouselItem);



import VueI18n from 'vue-i18n'
Vue.use(VueI18n);
const i18n = new VueI18n({
  locale: 'ru', // 语言标识
  messages: {
    'zh': require('./assets/lang/zh'),
    'en': require('./assets/lang/en'),
    'ru':require('./assets/lang/ru')
  }
})


Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  i18n,
  components: { App },
  template: '<App/>'
})

router.beforeEach((to, from, next) => {
  console.log(to);
  console.log(from);
  console.log(next);
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})




