import Vue from 'vue'
import Router from 'vue-router'
import index from '@/page/index'
import news from '@/page/news'
import enterprise from '@/page/enterprise'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
      meta: {
        title: 'Index'
      }
    },
    {
      path: '/news',
      name: 'news',
      component: news,
      meta: {
        title: 'News'
      }
    },
    {
      path: '/enterprise',
      name: 'enterprise',
      component: enterprise,
      meta: {
        title: 'Group'
      }
    }
  ]
})



