<template>
  <div>
    <top-vav :bgImg="bgImg"></top-vav>
    <div class="newsDetail">
      <div class="bgs"></div>
      <div class="detailFrame">
        <div class="detailUp">
          <div class="upLeft">
            <h2 class="upLeftTitle">{{$t('enterprise[0].tit')}}<img src="../assets/img/logo.png" /></h2>
            <p>{{$t('enterprise[0].con')}}</p>
          </div>
          <div class="upRight">
            <img src="../assets/img/newsDetailImg.png" />
            <div class="flicker"></div>
          </div>
        </div>
        <div class="detailDown">
          <h2>{{$t('enterprise[1].tit')}}</h2>
          <p>{{$t('enterprise[1].con')}}</p>
          <div class="downImg"><img src="../assets/img/map_px.png" /></div>
        </div>
      </div>
      <bottom-nav></bottom-nav>
    </div>
  </div>

</template>

<script>
  import topVav from '@/components/topNav'
  import bottomNav from '../components/bottomNav'
	export default {
		name: "enterprise",
		data() {
			return {
				msg: "123",
        bgImg:require('../assets/img/enterprise_banner.jpg')
			}
		},
    mounted(){
      $("html,body").animate({scrollTop:0},0)
    },
    components:{
      topVav,
      bottomNav
    }
	}
</script>

<style lang="scss" scoped>
  .flicker{
    position: absolute;
    top: 145px;
    right: 208px;
    background: #494c51;
    border-radius: 50%;
    width: 41px;
    height: 41px;
    animation: mymove .5s ease-in-out 0s infinite alternate;
  }
  @keyframes mymove{
    from {opacity:1}
    to {opacity:0.1}
  }
	.newsDetail{
		background-repeat: no-repeat;
		background-position: center 100%;
    background: rgba(28,31,37,0.8);
    position: relative;
    .bgs{
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-image: url('../assets/img/newsDetail.jpg');
      z-index: -1;
    }

		.detailFrame{
			padding: 220px 0 107px;
			max-width: 1200px;
			margin: 0 auto;
		}

		h2{
			font-size: 50px;
		}

		p{
			font-size: 12px;
			margin-top: 40px;
			line-height: 25px;
		}

		.detailUp{
			display: flex;
			display: -webkit-flex;

			.upLeft{
				flex: 1;
				color: #fefefe;
        .upLeftTitle{
          font-weight: normal;
          }
				.upLeftTitle img{
					max-width: 69px;
					width: 100%;
					vertical-align: middle;
					padding-bottom: 8px;
					margin-left: 33px;
				}
			}

			.upRight{
				flex: 1;
				text-align: right;
        position: relative;
				img{
					max-width: 330px;
					width: 100%;
				}
			}
		}

		.detailDown{
			margin-top: 269px;
			color: #fefefe;
      h2{
        font-weight: normal;
      }

			.downImg{
				text-align: center;
				margin-top: 113px;

				img{
					max-width: 901px;
					width: 100%;
				}
			}
		}
	}
</style>



