<template>
  <div class="big_boxs">
    <!--<img src="../assets/img/banner_one_zh.jpg" alt="" width="100%">-->
    <div class="container">
      <div class="content">
        <ul class="nav_ul">
          <router-link to="/enterprise">
            <li class="on_enterprise">{{$t('topNav.nav[0]')}}</li>
          </router-link>
          <li class="project" @click="go(1)">{{$t('topNav.nav[1]')}}</li>
          <li class="industry" @click="go(2)">{{$t('topNav.nav[2]')}}</li>
          <router-link to="/">
            <li class="logo"><img src="../assets/img/top_logo.png" alt=""></li>
          </router-link>
          <router-link to="/news">
            <li class="news_center">{{$t('topNav.nav[3]')}}</li>
          </router-link>
          <li class="contact_us" @click="go(3)">{{$t('topNav.nav[4]')}}</li>
          <li class="search">
            <i class="el-icon-search"></i>
            <input type="text">
          </li>
          <li class="switchover"> <el-dropdown trigger="click"  @command="handleCommand" placement="top">
      <span class="el-dropdown-link">
        {{$t('topNav.nav[5]')}}<i class="el-icon-arrow-down el-icon--right" style="color: #fff"></i>
      </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="1">中文</el-dropdown-item>
              <el-dropdown-item command="2">English</el-dropdown-item>
              <el-dropdown-item command="3">русский язык</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown></li>
        </ul>



      </div>
      <div class="bg_img" >
        <!--<img src="../assets/img/swiper1.jpg" alt="">-->
        <img :src="bgImg" alt="">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'topVav',
  data () {
    return {

    }
  },
  props:['bgImg'],
  methods:{
    go(val){
      val == 1?this.$router.push({name: 'index',params: {anchor: 1}}):false;
      val == 2?this.$router.push({name: 'index',params: {anchor: 2}}):false;
      val == 3?this.$router.push({name: 'index',params: {anchor: 3}}):false;
    },
    handleCommand(data){
      data == 1? this.$i18n.locale = 'zh':false;
      data == 2? this.$i18n.locale = 'en':false;
      data == 3? this.$i18n.locale = 'ru':false;
    }
  }
}
</script>
<style lang="scss" scoped>
  .big_boxs{
    width: 100%;
    .container{
      height: 468px;
      position: relative;
      .content{
        width: 1300px;
        /*height: 1000px;*/
        margin: 0 auto;
        .nav_ul{
          padding-top: 50px;
          display: flex;
          justify-content: space-between;
          &:after{
            content: '';
            clear: both;
            display: block;
          }
          li{
            float: left;
            font-size: 16px;
            font-weight: 500;
            color: #fff;
            cursor: pointer;
            height: 50px;
            line-height: 50px;
            min-width: 64px;
            .hover_nav{
              li{
                display: block;
                clear: both;
                height: 30px;
                line-height: 30px;
                font-size: 12px;
              }
            }
          }
          .project{
            /*margin-right: 130px;*/
            width: 194px;
          }
          .search{
            width: 150px;
            .el-icon-search{
              color: #fff;
            }
            input{
              width: 40%;
              background: none;
              border: none;
              border-bottom: 1px solid #fff;
              outline: none;
              color: #fff;
            }
          }
          .news_center{
            /*margin-right: 60px;*/
            width: 124px;
          }
          .logo{
            width: 170px;
            text-align: center;
            /*margin-right: 90px;*/
          }
          .contact_us{
            /*margin-right: 70px;*/
            width: 134px;
          }
          .industry{
            width: 164px;
            /*margin-right: 100px;*/
          }
          .on_enterprise{
            width: 194px;
            /*margin-right: 130px;*/
          }
          .switchover{
            .el-dropdown-link{
              color: #fff;
            }
          }
        }
      }
      .bg_img{
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
      }

    }
  }

</style>



