<template>
  <div>
      <div class="container_bottom">
        <div class="top_con">
          <div class="placeholder" v-if="ishold"></div>
          <div class="top_content">
            <div class="item">
              <div class="img">
                <img src="../assets/img/bottom_logo.png" alt="">
              </div>
              <div>{{$t('bottom.arr[0].con')}}</div>
            </div>
            <div class="item">
              <div>
                <h3>{{$t('bottom.arr[1].tit')}}</h3>
                <div>{{$t('bottom.arr[1].con')}}</div>
              </div>
            </div>
            <div class="item">
              <div>
                <h3>{{$t('bottom.arr[2].tit')}}</h3>
                <div v-for="item in $t('bottom.arr[2].con')">{{item}}</div>
              </div>
            </div>
          </div>
          <div class="bg_img" style="background: #1c1f25">
            <img :src="bottom_bg" alt="">
          </div>
        </div>

          <div class="bottom_nav">
            <div class="bottom_container">
              <ul class="bottom_ul">
                <router-link to="/enterprise"><li>{{$t('topNav.nav[0]')}}</li></router-link>
                <li @click="go(1)">{{$t('topNav.nav[1]')}}</li>
                <li @click="go(2)">{{$t('topNav.nav[2]')}}</li>
                <router-link to="/news"><li>{{$t('topNav.nav[3]')}}</li></router-link>
                <li @click="go(3)">{{$t('topNav.nav[4]')}}</li>
              </ul>
            </div>
          </div>
      </div>
  </div>
</template>

<script>
  export default {
    data(){
      return{

      }
    },
    methods:{
      go(val){
        val == 1?this.$emit('headCallBack', {params: {anchor: 1}})&&this.$router.push({name: 'index',params: {anchor: 1}}):false;
        val == 2?this.$emit('headCallBack', {params: {anchor: 2}})&&this.$router.push({name: 'index',params: {anchor: 2}}):false;
        val == 3?this.$emit('headCallBack', {params: {anchor: 3}})&&this.$router.push({name: 'index',params: {anchor: 3}}):false;


      }
    },
    props:['ishold','bottom_bg']
  }
</script>

<style lang="scss" scoped>
.container_bottom{
  .top_con{
    position: relative;
    .bg_img{
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
    }
    .placeholder{
      height: 193px;
    }
    .top_content{
      width: 1300px;
      margin: 0 auto;
      height: 228px;
      border-top: 1px solid #626262;
      color: #818283;
      .item{
        width: 33.3%;
        height: 100%;
        float: left;
        padding: 70px 0;
        box-sizing: border-box;
        display: flex;
        h3{
          margin-bottom: 10px;
        }
        img{
          margin-right: 20px;
        }
      }
      .item:nth-child(2){
        padding: 50px 30px;
      }
      .item:nth-child(3){
        padding: 50px 30px;
      }
    }
  }

  .bottom_nav{
    height: 86px;
    line-height: 86px;
    background: #1c1f25;
    .bottom_container{
      width: 1300px;
      margin: 0 auto;
      .bottom_ul{
        li{
          float: left;
          margin-right: 115px;
          color: #999a9a;
          font-size: 15px;
          cursor: pointer;
        }
      }
    }

  }
}
</style>



