<template>
  <div class="bg">
    <div class="more">
      <el-collapse accordion>
        <el-collapse-item>
          <template slot="title">MORE </template>
          <!--<div class="con">与现实生活一致：与现实生活的流程、逻辑保持一致，遵循用户习惯的语言和概念；</div>-->
          <!--<div class="con">在界面中一致：所有的元素和结构需保持一致，比如：设计样式、图标和文本、元素的位置等。</div>-->
          <div class="con" v-for="item in more_con">{{item}}</div>
        </el-collapse-item>
      </el-collapse>
    </div>

  </div>
</template>

<script>
  export default {
    data(){
      return{

      }
    },
    props:['more_con']
  }
</script>

<style lang="scss">
  .el-collapse-item__wrap{
    width: 530px;
    background: none !important;
    border-bottom: none !important;
  }
  .el-collapse{
    border-bottom: none !important;
  }
  .el-collapse-item__content{
    color: #a0a0a0 !important;
    /*border: 1px solid #fff !important;*/
  }
  .el-collapse-item__header{
    background: none !important;
    border: 1px solid #a0a0a0 !important;
    color: #a0a0a0 !important;
    text-align: center;
    border-bottom:1px solid #a0a0a0 !important;
    border-top: none !important;
    height: 30px !important;
    line-height: 30px !important;
  }
  .el-collapse{
    border-top: none !important;
  }

  .el-collapse-item__arrow{
    line-height: 30px !important;
  }

  .el-collapse-item__content{
    padding-top: 20px;
    padding-right: 20px;
  }

.bg{
  background: #1c1f25;
  margin-top: 30px;
  .more{
    width: 98px;
    height: 0;
    border: 1px solid #707070;
    .con{
    }
  }
}
</style>



